import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import { Pane, Heading, Button, AddIcon, EditIcon, BoxIcon, Code, SideSheet } from 'evergreen-ui'
import ConfigService from '../../@services/Config';

class PageConfigOverview extends Component {
  constructor(props)
  {
    super(props)
    this.state = {
      menuStruct: props?.menuStruct,
      isShown: false,
      configString: JSON.stringify(ConfigService.config, null, 4)
    }
  }
  componentDidMount()
  {
    if (!this.state.configString)
      this.setState({
        configString: JSON.stringify(ConfigService.config, null, 4)
      })
  }
  render() {
    return (
      <article>
        <Pane display="flex" padding=".5em" background="tint2">
          <Pane flex={1} alignItems="center" display="flex">
            <Heading size={600}>Config Overview</Heading>
          </Pane>
          <Pane>
            <NavLink to={`/${this.props.match.params.siteKey}/templates`}><Button appearance="minimal" iconBefore={AddIcon}>Add Template</Button></NavLink>
            <Button appearance="minimal" iconBefore={EditIcon} onClick={(e) => { e.preventDefault(); this.setState({ isShown: true, configString: JSON.stringify(ConfigService.config, true, 4) }) }}>View Code</Button>
            <Button appearance="minimal" iconBefore={BoxIcon} onClick={(e) => { e.preventDefault(); this.setState({ generateShown: true }) }}>Generate Package</Button>
          </Pane>
        </Pane>
        <Pane display="flex" flexWrap="wrap">
          {
            this.state.menuStruct.map((item, index) => (
              <Pane className="" key={index} flex="0 0 calc(25% - 2em)" borderRadius={5} height={128} display="flex" justifyContent="center" alignItems="center" margin="1em" elevation={1} border="default">
                <NavLink to={`/${this.props.match.params.siteKey}/config/${item.key}`}>
                  <Heading size={500}>{item.label}</Heading>
                </NavLink>
              </Pane>
            ))
          }
        </Pane>
        <SideSheet
          isShown={this.state.isShown}
          onCloseComplete={() => this.setState({ isShown: false })}
        >
          <Code codeString={this.state.configString} />
        </SideSheet>
        <SideSheet
          isShown={this.state.generateShown}
          onCloseComplete={() => this.setState({ generateShown: false })}
        >
          <h1>GENERATE_CODE</h1>
        </SideSheet>
      </article>
    )
  }
}

export default PageConfigOverview;